@import './module/vars';
@import './module/comm';

html,
body {
	font-size: 14px;
}

table {
	table-layout: fixed;
	width: 100%;
	border-collapse: collapse;
	border: 1px solid $light;

	td,
	th {
		vertical-align: middle;
		border: 1px solid $light;
		line-height: 1.8em;
		padding: 6px;
	}
}

#mainNav {
	> .navbar-brand {
		> img {
			width: 80px;
			height: 80px;
		}
	}

	.nav-item {

		&.active {
			> .nav-link {
				background-color: #5a6268;
				color: #fff;
			}
		}
		> .nav-link {
			padding-left: 1rem ;
			padding-right: 1rem ;
			font-weight: bold;
			font-size: 16px;
		}
	}

	button {
		padding-left: 1em;
		padding-right: 1em;
	}
}

.carousel-item.active,
.carousel-item-next,
.carousel-item-prev {
	display: block;
}

.wrapper {
	margin-left: auto;
	margin-right: auto;

	@media (min-width: $breakpoint-lg) {
		max-width: $lg-page-width;
	}
	/* @media (min-width: $breakpoint-lg) and (max-width: $breakpoint-xl){
        background-color: $pink;
		max-width: $lg-page-width;
	} */
	@media (min-width: $breakpoint-xs) and (max-width: $breakpoint-lg) {
		max-width: $md-page-width;
	}
}
.fa-eraser {
	width: 15px;
	margin: -25px 10px;
	float: right;
}

.tb {
	display: table;
	width: 100%;
	height: 100%;
	overflow-y: auto;
}
.tr {
	display: table-row;
}
.td {
	display: table-cell;
	padding: 5px;
	border: 1px solid #ddd;
	border-collapse: collapse;
}
.gridItem {
	display: inline-block;
	border: 1px solid #aaa;
	width: 116px;
	height: 116px;
	margin-left: -1px;
	margin-bottom: -1px;
}
.subTitle {
	position: relative;
	margin-top: -2.5em;
	line-height: 2.5em;
	background-color: rgba(255, 255, 255, 0.7);
	color: $black;
}
.company-logo {
	max-height: 100px;
	max-width: 100px;
}
a {
	// color: $dark;
}
a:hover {
	// color:$secondary;
}

.bg-secondary-light {
	background-color: $secondary-light;
}
.text-black {
	color: $black;
}
.text-gray {
	color: $gray;
}
.text-brown {
	color: $brown;
}
.font-12 {
	font-size: 0.857rem;
}
.font-14 {
	font-size: 1rem;
}
.font-16 {
	font-size: 1.143rem;
}
.font-20 {
	font-size: 1.429rem;
}
.font-24 {
	font-size: 1.75rem;
}
.font-30 {
	font-size: 2.14rem;
}
.img-fit {
	width: 100%;
}
.icon-inline {
	height: 0.9em;
	vertical-align: baseline;
}


.divDesignerCard {
	&:hover {
        background-color: $yellow;
        text-decoration: none;
        
        * {
            color: #fff;
        }
    }

    h4 {
        color: $yellow;
    }

    div{
        color:$dark;
    }
}
.divWorker {
	.card-title {
		border-bottom: 1px dashed gray;
	}
}